import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/views/layout'
// import Login from '@/views/login'
// import Register from '@/views/register'
// import Detail from '@/views/detail'

// import Article from '@/views/article'
// import Collect from '@/views/collect'
// import Like from '@/views/like'

import { getToken } from '@/utils/storage'
// import User from '@/views/user'
// 异步组件的写法，配合路由使用，该组件不会直接加载，会在路由解析时才加载
const Detail = () =>
  import('@/views/detail')
const Register = () =>
  import('@/views/register')
const Login = () =>
  import('@/views/login')
const Article = () =>
  import('@/views/article')
const Collect = () =>
  import('@/views/collect')
const Like = () =>
  import('@/views/like')
const User = () =>
  import('@/views/user')

Vue.use(VueRouter)
const router = new VueRouter({
  // 路由规则表
  routes: [{
    path: '/',
    component: Layout,
    redirect: '/article',
    children: [
      { path: '/article', component: Article },
      { path: '/collect', component: Collect },
      { path: '/like', component: Like },
      { path: '/user', component: User }
    ]
  },
  { path: '/login', component: Login },
  { path: '/register', component: Register },
  { path: '/detail/:id', component: Detail }
  ]

})

// 在每一个路由在真正访问之前，都会先经过路由前置导航守卫
// 只有前置守卫放行了，才能访问到对应的页面

// 每个路由即将被访问匹配时，都会调用beforeEach()的回调函数
// to : 往哪去，要去的路由信息对象（路径，参数...）
// from : 从哪个地址过来的
// next : 是否放行
//      (1)  next() 放行
//      (2)  next(path) 拦截某路由路径

const whiteList = ['/login', '/register']
// 导航守卫存在的意义：可以加拦截判断
// 1.判断用户有没有token，如果有，直接放行
// 2.没有token，是不是要去白名单的页面（登录，注册），这些可以访问
// 3. 没有token，要去的还是首页等需要授权的页面，拦截到登录
router.beforeEach((to, from, next) => {
  const token = getToken()
  if (token) {
    next()
  } else {
    // 没有token令牌的人
    // 如果去白名单的页面，next
    if (whiteList.includes(to.path)) {
      next()
    } else {
      next('/login')
    }
  }
})

export default router
